import { useState, useEffect } from 'react';
import '../boosting-tablet/BoostingApp-styles.css';
import BoostingInsideApp  from '../boosting-tablet/components/BoostingInsideApp';
import Notification from './components/NotificationApp';
import Settings from '../boosting-tablet/components/Settings'
import BennysApp from './components/BennysApp'
import { fetchNui } from '../utils/fetchNui';
import ConfigIcon from '../boosting-tablet/static/cog.svg'
import NotiIcon from '../boosting-tablet/static/comment.svg'
import WifiIcon from '../boosting-tablet/static/wifi.svg'
import BuyApp from './components/BuyApp';

export const isEnvBrowser = () => !(window).invokeNative


function BoostingApp() {
  const [App1, setApp1] = useState(false);
  const [App2, setApp2] = useState(false);
  const [Setting, setSetting] = useState(false);
  const [Notifi, setNotifi] = useState(false);
  const [EnableApp, setEnableApp] = useState(true)
  const [Wallpaper, setWallpaper] = useState(() => (JSON.parse(localStorage.getItem("boostingLaptop")) || "https://imgur.com/o4VGrTt.png"));
  const [ToBrowser, setToBrowser] = useState(false);
  const [showBoosting, setshowBoosting] = useState({visible: false || isEnvBrowser()});
  const [DataBoosting, setDataBoosting] = useState({time: "00:00 12/12/2022"});
  
  const Provider = (data) => {
    if (data.action === "openApplication") {
        if (data.app === "OpenBoostingApp") {
          setshowBoosting({
            visible: data.show
          })
        }
    } else if (data.action === "UpdateValueUI") {
        if (data.app === 'BoostingApp') {
          setDataBoosting({time: data.data.time})
        } else if (data.app === "EnableApps") {
          setEnableApp(data.data.BennysApp)
        }
    }
  }

  useEffect(() => {
      const EventListener = (event) => {
          if (event.data) {
            Provider(event.data)
          }
      }
      window.addEventListener("message", EventListener)
  }, [])

  useEffect(() => {
    localStorage.setItem("boostingLaptop", JSON.stringify(Wallpaper));
  }, [Wallpaper]);

  useEffect(() => {
    function handleEscapeKey(event) {
      if (event.code === 'Escape') {
        setshowBoosting({visible: false})
        fetchNui('ev-boosting:closeBoostingApp', {})
      }
    }
  
    document.addEventListener('keydown', handleEscapeKey)
    return () => document.removeEventListener('keydown', handleEscapeKey)
  }, [showBoosting, setshowBoosting])

  const openBoosting = () => {
    setApp1(true);
  }

  const openBennys = () => {
    setApp2(true);
  }

  const openSettings = () => {
    setSetting(true);
  }

  const OpenNotifications = () => {
    setNotifi(true);
  }

  const openFinance = () => {
    setToBrowser(true)
  }

  const apps = [
    {
      app: 'Orter',
      icon: 'fa-solid fa-joint',
      color: '#086c4e',
      open: () => {
        setshowBoosting({visible: true})
      }
    },
    {
      app: 'Tow Service',
      icon: 'fa-solid fa-tower-observation',
      color: '#981334',
      open: () => {
        setshowBoosting({visible: true})
      }
    },
    {
      app: 'Unkown',
      icon: 'fa-solid fa-user-secret',
      color: '#040006',
      open: () => {
        setshowBoosting({visible: true})
      }
    },
    {
      app: 'Dodo',
      icon: 'fa-solid fa-crow',
      color: '#20304e',
      open: () => {
        setshowBoosting({visible: true})
      }
    },
  ]


  return (
    
    
    <div style={ showBoosting.visible ? { visibility: 'visible' } : { visibility: 'hidden' }}>
      <div className="rectangule">
        <div className="container" style={{backgroundImage: `URL('${Wallpaper}')`}}>
          <div className="windows_apps">
            <button className="win_apps"><img height={50} width={50} alt="img" src="https://imgur.com/6RPZ0KP.png"/>Recyle Bin</button>
            <button className="win_apps"><img alt="img" src="https://imgur.com/X1kbasV.png"/>Stuff</button>
            <button onClick={openBoosting} className="win_apps" ><img height={40} width={40} alt="img" src="https://imgur.com/FEPqpLc.png"/>{'Boosting'}</button>
            {EnableApp && <button onClick={openBennys} className="win_apps" ><img height={40} width={40} alt="img" src="https://imgur.com/kGxkXTk.png"/>{'Bennys Parts'}</button>}
          </div>
          <BuyApp counter={ToBrowser} setCounter={setToBrowser} />
          <BennysApp counter={App2} setCounter={setApp2} />
          <BoostingInsideApp counter={App1} setCounter={setApp1} />
          <Notification counter={Notifi} setCounter={setNotifi} />
          <Settings counter={Setting} setCounter={setSetting} setWallpaper={setWallpaper} />
          <div className="windows_bar">
              <div className='fix_bar'>
                <button className="color4" style={{}}><img alt="img" src="https://i.imgur.com/L8w5avL.png" width="35" height="35"/></button>
                <button className="color4" style={{}}><img alt="img" src="https://imgur.com/QFOk7B1.png"width="24" height="24" /></button>
                <button className={ToBrowser ? "OpenedButton" : "color4"} style={{}} onClick={openFinance}><img alt="img"src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/e1/Google_Chrome_icon_%28February_2022%29.svg/768px-Google_Chrome_icon_%28February_2022%29.svg.png" width="24" height="24"/></button> 
                {App1 && <button className="OpenedButton" style={{}}><img alt="img"src="https://imgur.com/FEPqpLc.png" height={24} width={24}/></button>}
                {App2 && <button className="OpenedButton" style={{}}><img alt="img"src="https://imgur.com/kGxkXTk.png" height={24} width={24}/></button>}
                {/* {ToBrowser && <button className="OpenedButton" style={{}}><img alt="img"src="https://imgur.com/O10Rx0A.png" height={24} width={24}/></button>} */}
              </div>
              <button onClick={openSettings} className="color"><img alt="img"src={ConfigIcon} className="filter-white" style={{color: "white"}} height={17} width={17}/></button>
              <button className="color"><img alt="img" src={WifiIcon} style={{color: "white"}} className="filter-white" height={17} width={17}/></button>
              <div className="textohora">{DataBoosting.time}</div>
              <button onClick={OpenNotifications} className="color"><img alt="img"src={NotiIcon} style={{color: "white"}} className="filter-white" height={17} width={17}/></button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BoostingApp;
