import React, {useState, useEffect, useRef} from 'react'
import TextField from '@mui/material/TextField';
import BTN1 from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import Slide from '@mui/material/Slide';
import InputAdornment from '@mui/material/InputAdornment';
import { makeStyles } from "@material-ui/styles";


const STATIC_PRESET_BACKGROUNDS = [
    {name: "Ferrar", url: "https://imgur.com/wgUqskt.png"},
    {name: "Yung", url: "https://imgur.com/1KFfsh7.png"},
    {name: "Dirft", url: "https://cdnb.artstation.com/p/assets/images/images/003/233/947/4k/khyzyl-saleem-gtr2017nismo.jpg"},
    {name: "GTR", url: "https://imgur.com/ifTiZfC.jpg"},
    {name: "AMG", url: "https://imgur.com/K6mwCI1.png"},
    {name: "R34", url: "https://cdnb.artstation.com/p/assets/images/images/033/476/155/4k/christophe-desse-3.jpg"},
    {name: "Lambo", url: "https://cdna.artstation.com/p/assets/images/images/001/667/318/large/khyzyl-saleem-rapideeditfinallow.jpg"},
    {name: "R342", url: "https://imgur.com/aLaieBN.png"},
    {name: "MCLAREN", url: "https://imgur.com/UN94OX0.png"},
    {name: "bmw", url: "https://imgur.com/x1UPjBm.png"},
    {name: "MCLAREN2", url: "https://imgur.com/MxoBDb4.png"},
    {name: "carrito", url: "https://imgur.com/WYtC6YK.jpg"},
]

const Settings = ({counter, setCounter, setWallpaper}) => {

    const ref = useRef()
    const ref2 = useRef()
    const [Pop, setPop] = useState(() => (JSON.parse(localStorage.getItem("popaudio")) || {pop: true}));
    const [imputValue, setimputValue] = useState("")
    const [OpenDefaultWallpaper, setOpenDefaultWallpaper] = useState(false)

    const SaveSettings = () => {
        //setCounter(false);
        //working on it when fivem is ready
    }

    const handleInputChange = (e) => {
        setimputValue(e.target.value)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        if (imputValue.trim().length > 10){
            setWallpaper(imputValue);
        }
    }

    const OpenPresets = () => {
        setCounter(false);
        setOpenDefaultWallpaper(true);
    }

    useEffect(() => {
      localStorage.setItem("popaudio", JSON.stringify(Pop));
    }, [Pop]);

    const handleChange = (event) => {
      setPop({pop: event.target.checked});
    };
    
    useEffect(() => {
        const checkIfClickedOutside = (e) => {
          if (counter && ref.current && !ref.current.contains(e.target)) {
            setCounter(false);
          }
        }
    
        document.addEventListener("mousedown", checkIfClickedOutside)
    
        return () => {
          document.removeEventListener("mousedown", checkIfClickedOutside)
        }
    }, [counter, setCounter])

    useEffect(() => {
        const checkIfClickedOutside = (e) => {
          if (OpenDefaultWallpaper && ref2.current && !ref2.current.contains(e.target)) {
            setOpenDefaultWallpaper(false);
          }
        }
    
        document.addEventListener("mousedown", checkIfClickedOutside)
    
        return () => {
          document.removeEventListener("mousedown", checkIfClickedOutside)
        }
    }, [OpenDefaultWallpaper, setOpenDefaultWallpaper])

    const useStyles = makeStyles({
        root: {
          "& .MuiInput-underline:after": {
            borderBottomColor: "grey"
          },
          "& .MuiInput-underline:before": {
            borderBottomColor: "grey"
          },
          "& .MuiInput-underline:hover:not(.Mui-focused):before": {
            borderBottomColor: "grey"
          },
          '& .MuiInputBase-root': {
            color: 'grey',
        },
          
        },
    });

    const classes = useStyles();

    return (   
        <div>
            <Slide direction="left" in={counter} mountOnEnter unmountOnExit timeout={250}>
                <div className='settingsContainer' ref={ref}>
                    <div className="texto settings-text">Settings</div>
                    <form onSubmit={handleSubmit } autoComplete="off">
                    <TextField
                        className="textfix"
                        id="input-with-icon-textfield"
                        label="Enter Background"
                        classes={classes}
                        InputLabelProps={{style : {color : 'white'}}} 
                        onChange={handleInputChange}
                        key={handleInputChange}
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                                <i className="fas fa-photo-video" style={{ color: "white"}}></i>
                            </InputAdornment>
                          ),
                        }}
                        variant="standard"
                    />
                    </form>
                    <div className="btnfix">
                        <label htmlFor="contained-button-file">
                            <BTN1 variant="contained" component="span" sx={{bgcolor: 'rgb(19, 85, 167)', fontSize: '0.8125rem', ':hover': { bgcolor: '#69a74f', color: 'white'}}} onClick={SaveSettings}>
                              SAVE
                            </BTN1>
                            <BTN1 variant="contained" component="span" className="fixbtn2" sx={{bgcolor: 'rgb(19, 85, 167)' , fontSize: '0.8125rem', ':hover': { bgcolor: '#69a74f', color: 'white'}}} onClick={OpenPresets}>
                              PRESETS
                            </BTN1>
                        </label>
                        <div className="checkboxFix">
                          <FormControlLabel control={<Checkbox checked={Pop.pop} onChange={handleChange} style={{ color: "#F2A365"}}/>} color="primary" label={<span style={{ color: "white"}}>Notification Audio</span>}/>
                        </div> 
                    </div>
                </div>
            </Slide>
            <Slide direction="up" in={OpenDefaultWallpaper} mountOnEnter unmountOnExit timeout={250}>
                <div className='PresetsContainer' ref={ref2}>
                    <div className="texto settings-config ">Preset Backgrounds</div>
                    <div className='bgPresetsContainer'>
                        {STATIC_PRESET_BACKGROUNDS.map( (bg) => (
                            <button onClick={() => setWallpaper(bg.url)} className="bgButton" key={bg.url}>
                                <img alt="img"src={bg.url} className="bgcontainer"/>
                            </button>
                        ))}
                    </div>
                </div>
            </Slide>
        </div>
    )
}

//
export default Settings
